* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

li {
  list-style: none;
}

.short-desc {
  width: 400px;
  height: 200px;
  background-color: #1e5375;
  border-radius: 4px;
  margin-top: 10px;
  padding: 15px;
}

.country-list_flag {
  width: 60px;
  height: 40px;
}

.country-list_name {
  font-size: 25px;
  color: #fafafa;
}
.country {
  width: 400px;
  height: 250 px;
  background-color: #262b72;
  border-radius: 4px;
  padding: 15px;
}

.capital-flag {
  width: 80px;
  height: 50px;
}

.desc-text {
  font-size: 25px;
  font-weight: 500;
  color: #fafafa;
}
